<template>
  <div id="nav">
    <transition :name="transitionName">
      <router-view class="Router"></router-view>
    </transition>
      
      <cube-tab-bar
        v-model="selectedLabelDefault"
        :data="tabs"
        @click="clickHandler"
        @change="changeHandler"
        class="botnav"
        >
    </cube-tab-bar>
    <i class="addMallSum">{{ countSum }}</i>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
 data () {
    return {
      transitionName: 'slide-right',
      selectedLabelDefault: '首页', //底部导航栏默认高亮
      tabs: [{
        label: '首页',
        icon: 'cubeic-home'
      }, {
        label: '分类',
        icon: 'cubeic-tag'
      }, {
        label: '搜索',
        icon: 'cubeic-search'
      }, {
        label: '购物车',
        icon: 'cubeic-mall'
      },{
        label: '我的',
        icon: 'cubeic-person'
      }]
    }
  },
  methods: {
    clickHandler (label) {
      // if you clicked home tab, then print 'Home'

    },
    changeHandler (label) {
      // if you clicked different tab, this methods can be emitted
      switch(label) {
        case '首页':
          this.$router.push({path: '/nav/index'})
          break;
        case '分类':
          this.$router.push({path: '/nav/tag'})
          break;
        case '搜索':
          this.$router.push({path: '/nav/search'})
          break;
        case '购物车':
          this.$router.push({path: '/nav/mall'})
          break;
        case '我的':
          this.$router.push({path: '/nav/person'})
          break;
      }
    },
  },
  computed: {
    ...mapGetters({
      countSum: 'countSum'
    })
  },
  //让底部导航栏在哪个选项就默认高亮哪个选项
  created() {
    switch (this.$route.path) {
      case '/nav/index':
        this.selectedLabelDefault = '首页'
        break;
      case '/nav/tag':
        this.selectedLabelDefault = '分类'
        break;
      case '/nav/search':
        this.selectedLabelDefault = '搜索'
        break;
      case '/nav/mall':
        this.selectedLabelDefault = '购物车'
        break;
      case '/nav/person':
        this.selectedLabelDefault = '我的'
        break;
    
      default:
        break;
    }
    
  }
}
</script>

<style lang="stylus">
  .cube-tab-bar.botnav
    position fixed
    bottom 0
    left 0
    width 100%
    z-index 1000
    background-color #fff
    .cube-tab div
      font-size 16px
      padding-top 10px
    i 
      font-size 20px
  .Router
    position absolute
    width 100%
    transition all 0.8s ease
  .slide-left-enter, .slide-right-leave-active
    opacity 0
    -webkit-transform translate(100%,0)
    transform  translate(100%,0)
  .slide-left-leave-active, .slide-right-enter
    opacity 0
    -webkit-transform translate(-100%,0)
    transform  translate(-100%,0)
  .addMallSum
    border-radius 50%
    background-color red
    font-size 18px
    bottom  40px
    position fixed
    z-index 1001
    right 22%
    width 18px
    height 18px
    color #fff
</style>